.header {
  background: #2c2c2c;
  height: 100vh;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  position: relative; }
  .header__top {
    display: flex;
    align-items: center;
    padding-top: 52px;
    justify-content: space-between; }
  .header__top_left {
    display: flex; }
  .header__navbar_list {
    display: flex; }
  .header__logo {
    padding-right: 78px;
    animation: animate 10s linear forwards; }
  .header__navbar_item {
    list-style: none;
    padding-right: 64px; }
  .header__navbar_link, .header__navbar_link_last {
    text-decoration: none;
    color: #fff;
    position: relative;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 3px;
    font-size: 13px; }
  .header__navbar_link:before {
    position: absolute;
    content: "";
    left: -1px;
    bottom: -7px;
    height: 2px;
    width: 100%;
    background: #2060f6;
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.4s ease; }
  .header__navbar_link:hover:before {
    transform: scaleX(1);
    transform-origin: left; }
  .header__navbar_link_last:before {
    content: "";
    width: 100%;
    background: #2060f6;
    height: 2px;
    position: absolute;
    bottom: -7px;
    left: -1px;
    transform-origin: left;
    transition: transform 0.4s ease; }
  .header__navbar_link_last:hover:before {
    transform-origin: right;
    transform: scaleX(0); }
  .header__text {
    margin-top: 160px; }
  .header__title {
    color: #fff;
    font-size: 64px;
    font-weight: 600;
    margin-bottom: 32px;
    letter-spacing: -2px;
    line-height: 68px; }
  .header__descr {
    font-size: 18px;
    color: #fff;
    margin-bottom: 80px; }
  .header__button {
    text-decoration: none;
    color: #fff;
    background: #2060f6;
    padding: 15px 70px;
    border-radius: 8px;
    transition: 0.3s ease-out; }
  .header__button:hover {
    color: #2060f6;
    background: #fff; }
  .header__slider {
    position: absolute;
    right: 0;
    bottom: -7px; }
  .header__slider_1_picture {
    border-top-left-radius: 30px;
    border-bottom-right-radius: 30px;
    height: 540px; }
  .header__slider_1 {
    position: relative; }
  .header__slider_descr {
    position: absolute;
    bottom: 50px;
    left: 145px; }
  .header__slider_arrow_left {
    background: url(../images/slider/left.svg);
    background-repeat: no-repeat;
    background-position: center;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    position: absolute;
    background-color: #fff;
    bottom: 70px;
    left: 50px;
    cursor: pointer;
    transition: 0.2s ease-out; }
  .header__slider_arrow_right {
    background: url(../images/slider/right.svg);
    background-repeat: no-repeat;
    background-position: center;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    position: absolute;
    background-color: #fff;
    bottom: 70px;
    left: 90px;
    cursor: pointer;
    transition: 0.2s ease-out; }
  .header__slider_arrow_left:hover, .header__slider_arrow_right:hover {
    transform: scale(1.1); }
  .header__slider_arrow_left:active, .header__slider_arrow_right:active {
    transform: scale(0.9); }

