.vision {
  background: $c-white;
  height: 690px;
  display: flex;
  align-items: center;
  &__content {
    text-align: center;
  }
  &__title {
    font-size: 52px;
    color: $c-dark;
    font-weight: 600;
  }
  &__descr {
    font-family: $font-open;
    color: $c-light-dark;
    font-size: 24px;
    max-width: 750px;
    margin: auto;
    margin-bottom: 70px;
  }
  &__button {
    text-decoration: none;
    color: $c-white;
    background: $c-blue;
    padding: 15px 70px;
    border-radius: 8px;
    transition: 0.3s ease-out;
  }
  &__button:hover {
    color: $c-blue;
    background: $c-white;
    border: 1px solid $c-blue;
  }
}
